@import "components/variables.less";
@import "components/mixins.less";
@import "components/late.less";

.head-with-btns {
	&__right {
		.flex();
		.btn {
			margin-left: 10px;
			width: 130px;
		}
		
		.far {
			margin-right: 10px;
		}
	}	
	
	@media (max-width: 767px) {
		&__right {
			.btn {
				width: auto;
			}
			
			.far {
				margin-right: 0px;
			}			
		}
	}
}

//***************** Form 
.form-filiation {
	margin-bottom: 20px;
	
	.input-groups-periods {
		.form-control {
			flex: 1;
			text-align: center;
		}
	}
	
	.btn-fix-width {
		width: 115px;
	}
	
	.form-btns {
		.flex();
		justify-content: flex-end;
		margin-top: 10px;
		
		.btn {
			margin-left: 10px;
		}
	}
	
	@media (max-width: 991px) {
		.form-btns {
			margin-top: 0;
		}
	}
	
	@media (max-width: 767px) {
		.form-btns {
			flex-wrap: wrap;
			
			.btn {
				margin-left: 0;
			}
			
			.btn-upload {
				width: 100%;
				margin-bottom: 15px;
			}
			
			.btn-fix-width {
				flex: 1;
				margin-right: 30px;
				
				&:last-child {
					margin-right: 0;
				}
			}
		}
	}
}


.block-tabs {
    margin-top: 10px;
    margin-bottom: 20px;

    .nav-tabs {
        margin-top: 0;
        margin-bottom: 0;
    }
    
    &.block-tabs-prod {
    	.flex();
    	.flex-align-items(center);
    	width: 100%;
    	justify-content: space-between;
    	
		.nav-tabs {
            flex: 1;
        }    
        
        .btns-tabs {
        	.flex();
        	margin-left: 20px;
        	
        	.btn {
        		margin-left: 10px;
        	}
        }
    }
}

.btns-prod-xs {
	.flex();
	justify-content: space-between;
	
	.btn {
		flex: 1;
		margin-right: 15px;
		
		&:last-child {
			margin-right: 0;
		}
		
		.txt-visible-xs {
			display: none;
		}
	}
	
	@media (max-width: 479px) {
		.btn {			
			.txt-hidden-xs {
				display: none;
			}
			
			.txt-visible-xs {
				display: inline;
			}
		}
	}
}

.form-btns-calc {
    margin-top: 20px;
    
    .btn-default {
        margin-right: 30px;
    }

    .btn-info {
        padding-left: 0;
        padding-right: 0;

        .fa-chevron-circle-left {
            margin-right: 5px;
        }

        .fa-chevron-circle-right {
            margin-left: 5px;
        }
    }
    
    .btn-info-less {
        margin-right: 30px;
    }
    
    .btn-save {
        float: right;
    }
}

@media (min-width: 768px) and (max-width: 991px) {
    .form-btns-calc {
        .btn-info {
            font-size: 13px;
        }
    }
}

@media (max-width: 767px) {
    .form-production {
        margin-top: 15px;
    }

    .form-btns-calc {
        margin-top: 15px;

        .btn-save {
            float: none;
            display: block;
            width: 100%;
            clear: both;
        }
        
        .btn-info {
            margin-bottom: 15px;
        }
        
        .btn-info-less {
            float: left;
            margin-right: 0;
        }
        
        .btn-info-more {
            float: right;
        }            
    }   
}

.table-production {
    border-collapse: separate;
    border-bottom-width: 1px;
    border-top-width: 1px;
    border-left: 0px;
    border-right: 0px;
    
    > thead > tr {
        > th {
            text-align: center;
            border-top: 1px solid #ddd !important;
            border-left-color: transparent;
            border-right-color: transparent;
            
            &.cell-selected {
                border-left-color: #468cc8;
                border-right-color: #468cc8;
                border-top-color: #468cc8 !important;
                background-color: #f6f9fc;
            }
        }
    }
    
    > tbody > tr {
        > td {
            vertical-align: middle;
            text-align: center;
            border-top: 1px solid transparent;
            border-bottom: 1px solid #ddd;
            border-left-color: transparent;
            border-right-color: transparent;
            
            &:first-child {
                border-left: 1px solid transparent;
            }

            &:last-child {
                border-right: 1px solid transparent;
            } 
            
            &.cell-selected {
                border-left-color: #468cc8;
                border-right-color: #468cc8;
                background-color: #f6f9fc;
            }            
        }
        
        &.row-selected {
            > td {
                border-top-color: #468cc8;
                border-bottom-color: #468cc8;
                background-color: #f6f9fc;
                border-left-color: #f6f9fc;
                border-right-color: #f6f9fc;
                
                &:first-child {
                    border-left-color: #468cc8;
                }
    
                &:last-child {
                    border-right-color: #468cc8;
                }
                
                &.cell-selected {
                    border-left-color: #468cc8;
                    border-right-color: #468cc8;                    
                }
            }
        }
        
        &:last-child {
            > td.cell-selected {
                border-bottom-color: #468cc8;
            }
        }
    }
    
    .btn {
        background-color: transparent;
        padding-left: 3px;
        padding-right: 3px;
    }    
    
    .form-control {
        width: 45px;
        padding-left: 6px;
        padding-right: 6px;
        font-size: inherit;
        display: inline-block;
        text-align: center;
    }
    
    .row-head {
        > td {
            text-align: left;
            color: #a1a1a1;
            font-weight: bold;
            font-size: 12px;
            padding-left: 58px;
            border-bottom: 0px;
        }
    }
    
    .form-timework {
        white-space: nowrap;
        
        .form-control:nth-of-type(2) {
            margin-right: 3px;
        }
        
        .btn-add-time {
            color: #4ba34c;
        }
        
        .btn-del-time {
            color: #ff0000;
            
            i {
                position: relative;                
                top: -1px;
            }
        }            
    }
    
    .cell-timework {
        text-align: left;
        white-space: nowrap;
    }
    
    .cell-premium {
        background-color: #e1f6cc;
    }
    
    .cell-sum {
        background-color: #ededed;
    }
    
    .cell-fine-big {
        background-color: #ffd4d4;
        
        .form-control {
            background-color: #ffd4d4;
        }
    }
    
    .cell-fine-small {
        background-color: #ffffb2;
        
        .form-control {
            background-color: #ffffb2;
        }           
    }
    
    .row-total {
        td:first-child {
            padding-left: 52px;
        }
    }
    
    .cell-total {
        text-align: left;
        text-transform: uppercase;
        font-weight: bold;
    }
        
    .cell-remove {
        width: 20px;
    }
    
    .btn-add {
        color: #4ba34c;
    }
    
    .btn-del,
    .btn-cancel {
        color: #f60721;
    } 
    
    .cell-persacc {
        white-space: nowrap;
        text-align: center;
        
        .btn {
            margin-right: 10px;
            
            &:last-child {
                margin-right: 0;
            }
        }
        
        .form-control {
            width: 55px;
            margin-right: 5px;
        }
    } 
    
    .cell-states {
        padding-left: 0;
    }
    
    .cell-name {
        text-align: left;
    }    
}

@media (min-width: 992px) {
    .table-responsive-wrapper-production {
        .table-responsive2 {
            .table-production {
                > thead {
                    > tr {
                        > th {
                            &.cell-head-timework {
                                height: auto !important;
                            }
                        }
                    }
                }
            }
        }
    }
}

@media (max-width: 991px) { 
    .table-responsive-wrapper-production {
        .table-responsive2 {
            .table-production {
                > thead {
                    > tr {
                        > th {
                            border: 1px solid #ddd;
                            border-left-color: transparent;
                            border-right: 0;
                            border-bottom-color: transparent; 
                            
                            &.cell-selected {
                                border-left-color: #468cc8;
                                border-right-color: #468cc8;
                                border-top-color: #468cc8 !important;
                                border-bottom-color: #468cc8;
                                background-color: #f6f9fc;
                            }
                            
                            &:last-child {
                                border-bottom-color: #ddd;
                            }
                        }
                    }
                }

                > tbody {
                    > tr {
                        position: relative;
                        
                        > td {
                            border-bottom-color: transparent;
                            border-top: 1px solid #ddd;
                            
                            &.cell-selected {
                                border-top-color: #468cc8;
                                border-bottom-color: #468cc8;
                                background-color: #f6f9fc;
                                border-left-color: transparent;
                                border-right-color: transparent;
                            }
                            
                            &:last-child {
                                border-bottom-color: #ddd;
                            }
                        }
                        
                        &.row-selected {
                            > td {
                                background-color: #f6f9fc;
                                border-left-color: #468cc8;
                                border-right-color: #468cc8;
                                border-top-color: #ddd;
                                border-bottom-color: transparent;
                                
                                &:first-child {
                                    border-left-color: transparent;
                                }
                                
                                &.cell-name {
                                    border-top-color: #468cc8;
                                }
                    
                                &:last-child {
                                    border-right-color: #468cc8;
                                    border-bottom-color: #468cc8;
                                }
                                
                                &.cell-selected {
                                    border-top-color: #468cc8;
                                    border-bottom-color: #468cc8;
                                }
                            }
                        }
                        
                        &:last-child {
                            > td {
                                &.cell-selected {
                                    border-right-color: #468cc8;
                                }
                            }
                        }
                    }
                }
                
                .cell-states, 
                .cell-btn-del {
                    position: absolute;
                    top: 24px;
                    height: 35px;
                    line-height: 35px;
                    vertical-align: middle;
                    border: 0;
                    padding: 0 !important;
                    background-color: transparent !important;
                    z-index: 1;
                }
                
                .cell-btn-del {
                    left: 13px;
                    top: 23px;
                }
                
                .cell-states {
                    left: 27px;
                    width: 30px;
                    text-align: center;
                    
                    .fas {
                        margin-right: 1px;
                        
                        &:last-child {
                            margin-right: 0;
                        }
                    }
                }
                
                .cell-name {
                    padding-left: 55px;
                    height: 60px;
                    padding-top: 32px;
                    position: relative;
                    
                    &:before {
                        content: attr(data-post);
                        position: absolute;
                        top: 5px;
                        left: 15px;
                        background-color: transparent;
                        color: #a1a1a1;
                        font-weight: 700;
                        font-size: 12px;
                    }                    
                }
                
                .cell-total {
                    height: 60px;
                    padding-top: 32px;
                }
                
                .cell-head-name {
                    height: 60px;
                    
                    .cell-head_name {
                        padding-top: 24px;
                    }
                }
                
                .cell-timework {
                    min-height: 52px;
                }
                
                .cell-persacc {
                    padding-left: 7px;

                    .btn {
                        padding-left: 8px;
                        padding-right: 8px;
                    }
                }
                
                .cell-fine-prem {
                    padding-top: 0px;
                    padding-bottom: 0px;
                    height: 35px;
                }
            }
        }
    }
}

@media (min-width: 768px) and (max-width: 991px) {
    .table-responsive-wrapper-production {
        position: relative;
        margin-left: -15px;
        margin-right: -15px;
        
        .table-responsive2 {
            margin-left: 170px;
            width: -webkit-calc(~'100% - 170px');
            width: -moz-calc(~'100% - 170px');
            width: calc(~'100% - 170px');
            
            // Производство
            .table-production {
                margin-bottom: 0;

                > thead {
                    display: block;
                    width: 170px;
                    position: absolute;
                    top: 0;
                    left: 0;
                    z-index: 1;
                    .box-shadow(7px 0 5px -5px rgba(0, 0, 0, 0.25));
                    background-color: #fff;
                    border-right: 1px solid #ddd;
                    border-top: 1px solid #ddd;
                    border-bottom: 1px solid #ddd;
                    
                    > tr {
                        display: block;
                        
                        > th {
                            display: block;
                            position: relative;
                            white-space: nowrap;
                            text-align: left;
                            
                            .line-wrap {
                                display: block;
                            }
                            
                            &.cell-head-persacc,
                            &.cell-head-timework {
                                white-space: normal;
                                height: 48px;
                                line-height: 1.3;
                                .flex();
                                .flex-align-items(center);
                            }
                            
                            &.cell-head-timework {
                                height: 52px;
                            }
                        }
                    }
                }
                
                > tbody {
                    .flex();
                    
                    > tr {
                        .flex();
                        flex-direction: column;
                        position: relative;
                        
                        > td {
                            display: block;
                            text-align: left;
                            padding: 8px 15px;
                            
                            &.cell-name {
                                white-space: nowrap;
                            }
                            
                            &.cell-persacc,
                            &.cell-fine-prem {
                                .flex();
                                .flex-align-items(center);
                            }
                            
                            &.cell-persacc {
                                height: 48px;
                            }
                        }
                    }
                }
            }
            
            // Доставка
            .table-deliv {
                .cell-head-name {
                    height: auto;
                    
                    .cell-head_name {
                        padding-top: 0;
                    }
                }
                
                .cell-name {
                    height: auto;
                    padding-top: 8px;
                    padding-left: 15px;
                    
                    &.cell-name_states_yes {
                        padding-left: 30px;
                    }
                }
                
                .cell-total {
                    height: auto;
                    padding-top: 8px;
                }
                
                .cell-states {
                    left: 2px;
                    top: 0;
                }
                
                .cell-head-high {
                    height: 48px;
                    white-space: normal;
                    line-height: 1.3;
                }
                
                .cell-high {
                    height: 48px;
                    .flex();
                    .flex-align-items(center);
                }
            }
        }
    }
}

@media (max-width: 767px) {
    .table-responsive-wrapper-production {
        .table-responsive2 {

            // Производство
            .table-production {
                > thead {
                    border-right: 1px solid #ddd;
                    border-top: 1px solid #ddd;
                    border-bottom: 1px solid #ddd;

                    > tr {
                        > th {
                            // ФИО
                            &.cell-head-name {
                                height: 75px;

                                &:before {
                                    content: "\f007";
                                    margin-top: 42px;
                                }
                            }
                            
                            // Прем./личн. счёт
                            &.cell-head-account {
                                &:before {
                                    content: "\f2bb";
                                }
                            }
                            
                            // Списать/ начислить на лс
                            &.cell-head-persacc {
                                &:before {
                                    content: "\f0ec";
                                }
                            }
                            
                            // Разряд
                            &.cell-head-posts {
                                &:before {
                                    content: "\f47f";
                                }
                            }
                            
                            // Начало/конец работы
                            &.cell-head-timework {
                                height: 48px;

                                &:before {
                                    content: "\f2f2";
                                    margin-top: -9px;
                                    top: 50%;
                                }
                            }
                            
                            // Рабочее время
                            &.cell-head-time {
                                &:before {
                                    content: "\f4d3";
                                }
                            }
                            
                            // Компенсация
                            &.cell-head-compens {
                                &:before {
                                    content: "\f0e2";
                                }
                            }
                            
                            // Нагрузка
