                                &:before {
                                    content: "\f4c5";
                                }
                            }
                            
                            // Норматив выручки/ч
                            &.cell-head-receipts {
                                &:before {
                                    content: "\f571";
                                }                                
                            }
                            
                            // Коэфф. норм.
                            &.cell-head-coeff {
                                &:before {
                                    content: "\f009";
                                }                                 
                            }
                            
