                            &.cell-head-correct-time {
                                &:before {
                                    content: "\f017";
                                }                                
                            }                            
                            
                            // Премия за прев. нормы
                            &.cell-head-prem2 {
                                &:before {
                                    content: "\f164";
                                }
                            }
                            
                            // Депремирование
                            &.cell-head-deprem {
                                &:before {
                                    content: "\f165";
                                }
                            }

                            // Премия
                            &.cell-head-prem {
                                &:before {
                                    content: "\f055";
