                }
                
                > tbody {
                    > tr {
                        > td {
                            &.cell-name {
                                height: 52px;
                                padding-top: 2px;
                                padding-bottom: 0;
                                padding-left: 15px;
                                
                                &.cell-name_states_yes {
                                    padding-left: 30px;
                                }
                            }
                            
                            &.cell-total {
                                height: 52px;
                                padding-top: 8px;
                            }                            
                            
                            &.cell-states {
                                left: 2px;
                                top: 7px;
                            }
                        }
                    }
                }
