// Опоздания
.table-late {
    > thead > tr > th,
    > tbody > tr > td {
        &.load-delayed {
            border-left: 2px solid #ddd;
        }
    }
    
    > thead {
        > tr {
            &:first-child {
                > th {
                    border-bottom-width: 1px;
                }
            }
            
            > th {
                &.cell-head-time {
                    display: none;
                }
            }
        }
    }
    
    .js-show-modal-late {
        cursor: pointer;
    }
}

