@media (max-width: 767px) {
    .table-responsive-wrapper-late {
        .table-responsive2 {
            .table-late {
                border-top-width: 2px;

                > thead {
                    border-top-width: 1px;
                    border-bottom-width: 2px;

                    > tr {
                        > th {
                            border-bottom-width: 1px;
                            border-top-color: transparent;
                            border-bottom-color: transparent;
                            
                            &.cell-selected {
                                border-right-color: transparent;
                                border-bottom-color: #468cc8;
                            }

                            &.cell-head-time {
                                display: block;
                            }
                            
                            &.load-delayed {
                                border-left-width: 0;
                            }
                            
                            // Время
                            &.cell-head-time {
                                &:before {
                                    display: none;
                                }
                            }
                            
                            // КОРФ
                            &.cell-head-korf {
                                &:before {
                                    content: "\f43d";
                                }
                            }
                            
                            // Нагрузка
                            &.cell-head-load {
                                &:before {
                                    content: "\f4ce";
                                }
                            } 
                            
                            // Сотрудники
                            &.cell-head-worker {
                                &:before {
                                    content: "\f007";
                                }
                            }
                            
                            // Сотр. c норм.
                            &.cell-head-workernorm {
                                &:before {
                                    content: "\f507";
                                }
                            }   
                            
                            // Выручка
                            &.cell-head-receipts {
                                &:before {
                                    content: "\f158";
                                }
                            }
                            
                            // Опоздания
                            &.cell-head-late {
                                &:before {
                                    display: none;
                                }
                                
                                .cell-head_name {
                                    visibility: visible;
                                }
                            }
                            
                            // Кол-во заказов
                            &.cell-head-orders {
                                &:before {
                                    content: "\f4a1";
                                }
                            }                            
                        }
                    }
                }
                
                > tbody {
                    > tr {
                        > td {
                            text-align: center;

                            &.load-delayed {
                                border-left: 1px solid transparent;
                            }
                            
                            &.cell-selected {
                                border-top-color: #468cc8;
                                border-bottom-color: #468cc8;
                                border-right-color: transparent;
                                border-left-color: transparent;
                            }
                        }
                        
                        &.row-selected {
                            > td {
                                border-top-color: transparent;
                                border-bottom-color: #ddd;
                                border-right-color: #468cc8;
                                border-left-color: #468cc8;
                                
                                &.load-delayed {
                                    border-left-color: #468cc8;
                                }
                                
                                &.cell-selected {
                                    border-top-color: #468cc8;
                                    border-bottom-color: #468cc8;
                                }
                                
                                &:first-child {
                                    border-top-color: #468cc8;
                                }
                            }
                        }                        
                    }
                }
            }
        }
    }
    
    // повар-сушист
    .table-responsive-wrapper-sushi {
        .table-responsive2 { 
            .table-late {
                > thead {
                    > tr {
                        > th {
                            &.cell-sushi {
                                display: block;
                            }
                        }
                    }
                }
            }
        }
    }
    
    // водитель
    .table-responsive-wrapper-driver {
        .table-responsive2 { 
            .table-late {
                > thead {
                    > tr {
                        > th {
                            &.cell-driver {
                                display: block;
                            }
                        }
                    }
                }
            }
        }
    }    
    
    // повар-пиццмейкер
    .table-responsive-wrapper-pizza {
        .table-responsive2 { 
            .table-late {
                > thead {
                    > tr {
                        > th {
                            &.cell-pizza {
                                display: block;
                            }
                        }
                    }
                }
            }
        }
    }  
    
    // безнал
    .table-responsive-wrapper-card {
        .table-responsive2 { 
            .table-late {
                > thead {
                    > tr {
                        > th {
                            &.cell-card {
                                display: block;
                            }
                        }
                    }
                }
            }
        }
    }    
