.table-production-cont {
    width: 100%;
    position: relative;
}

.panel-group-operat {
    position: absolute;
    right: 0px;
    white-space: nowrap;
    
    .btn {
        margin-left: 6px;
        
        -webkit-box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.3);
           -moz-box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.3);
                box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.3);        
    }
}

.table-production-print {
    .row-head > td {
        padding-left: 8px;
    }
    .row-total td:first-child {
        padding-left: 8px;
    }
}

.legend-print {
    font-size: 12px;
    
    .item {
        margin-bottom: 5px;
    }
    
    .item-dt {
        float: left;
        font-weight: bold;
        white-space: nowrap;
        text-align: right;
        width: 55px;
    }
    
    .item-dd {
        margin-left: 70px;
    }
}

.modal-dialog-fineprem {
	width: 500px;
	
	&__form {
		.btn-group {
			margin-top: 25px;
			width: 100%;
			
			> .btn {
				width: 50%;
				&.active {
					.box-shadow(none);
				}
			}
			
			.btn-fine {
				&.active {
					background-color: #FFA9B1;
				}
			}
			
			.btn-prem {
				&.active {
					background-color: #e1f6cc;
				}				
			}
		}
		
		.form-btns {
			width: 100%;
			.flex();
			justify-content: flex-end;
			
			.btn {
				padding-left: 25px;
				padding-right: 25px;
				margin-left: 15px;
				
				&:first-child {
					margin-left: 0;
				}
			}
		}
	}
	
	@media (max-width: 767px) { 
		&__form {
			.btn-group {
				margin-top: 0;
			}
			
			.form-btns {
				.btn {
					flex: 1;
				}
			}
		}
	}
}

.savedcalc__modal {
	width: 730px;
	.modal-header {
		border: none;
		position: absolute;
		right: 0;
		z-index: 10;
	}
	.modal-body {
		padding: 40px 30px;
	}
	&-table {
		margin-bottom: 0;
		> thead {
			> tr {
				> th {
					font-size: 11px;
					line-height: 13px;
					&.cell-head-datetime {
						width: 112px;
					}
				}
			}
		}
		> tbody {
			> tr {
				> td {
					font-weight: 300;
					&.cell-problem-icon {
						padding: 8px 0;
						font-size: 15px;
						color: red;
					}
					p:last-child {
						margin: 0;
					}
				}
			}
		}
	}
	@media (max-width: 767px) {
		width: 100%;
		.modal-body {
			padding: 50px 15px 30px;
		}
		&-table {
			display: block;
			> thead {
				display: block;
				> tr {
					.flex();
					border-bottom: 1px solid #ddd;
					> th {
						border-bottom: none;
						padding: 8px 0;
						&.cell-head-icon {
							flex-basis: 4%;
						}
						&.cell-head-datetime {
							flex-basis: 31%;
							margin: auto;
						}
						&.cell-head-distance {
							flex-basis: 16%;
							margin: auto;
						}
						&.cell-head-time {
							flex-basis: 37%;
							margin: auto;
						}
					}
				}
			}
			display: block;
			> tbody {
				display: block;
				> tr {
					.flex();
					flex-wrap: wrap;
					> td {
						&.cell-problem-icon {
							flex-basis: 4%;
							border-top: 1px solid #ddd;
						}
						&.cell-route {
							flex-basis: 96%;
							border-top: 1px solid #ddd;
							padding: 8px 8px 0 8px;
						}
						&.cell-datetime {
							font-size: 14px;
							flex-basis: 40%;
							padding: 8px 8px 8px 24px;
							border: none;
						}
						&.cell-distance {
							font-size: 14px;
							flex-basis: 20%;
							border: none;
						}
						&.cell-time {
							font-size: 14px;
							flex-basis: 40%;
							border: none;
						}
					}
				}
			}
		}
	}
}